.m-slider {
  max-width: 575px;
  margin-left: 8px;
}

.m-slider__slides {
  display: flex;
  /* column-gap: 16px; */
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}

.m-slider__slide {
  min-width: 95%;
  scroll-snap-align: start;
  padding: 0 8px;
}

.m-slider__slide__top {
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 122%;
  margin-bottom: 8px;
}

.m-slider__slide__top--active {
  color: #cf0a2c;
}

.m-slider__slide-content {
  padding: 24px 16px 84px;
  color: #fff;
  background-color: #999999;
  font-weight: 400;
  font-size: 16px;
  line-height: 122%;
}
