main {
  @include xs-only {
    margin-left: 0;
  }
  margin-left: calc(10% + 120px); /* Same as the width of the sidenav */
  font-size: 28px; /* Increased text to enable scrolling */
}
header {
  @include xs-only {
    height: 100%;
    margin-top: 50px;
    margin-left: 0;
  }
  @include sm-only {
    height: 100%;
  }
  margin-left: calc(10% + 120px);
  height: 250px;
}
footer {
  margin-top: 50px;
  @include xs-only {
    margin-left: 0;
  }
  margin-left: calc(10% + 120px);
}
