.slider {
  // max-width: 1056px;
  display: flex;
  flex-direction: column;
  &__nav-items {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__nav-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
    cursor: pointer;
    p {
      margin-top: 15px;
    }
    transition: all 0.3s;
  }

  &__icon--active {
    transition: all 0.3s;
    path {
      fill: #cf0a2c;
    }
  }

  &__nav-item:hover {
    color: #cf0a2c;
    path {
      fill: #cf0a2c;
    }
  }

  &__nav-item-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
  }

  &__nav-item--active {
    color: #cf0a2c;
    cursor: default;
  }

  &__slides {
    display: flex;
    flex-grow: 1;
    overflow-x: hidden;
  }

  &__slide {
    display: flex;
    flex-shrink: 0;
    width: 100%;
    background-color: #999999;
  }

  &__slide-image {
    width: 397px;
    background: no-repeat;
    background-size: cover;
  }

  &__slide-content {
    width: 539px;
    flex-grow: 1;
    color: #fff;
    padding: 82px 60px;
  }

  &__slide-content-title {
    font-weight: 700;
    font-size: 30px;
    line-height: 44px;
    margin-bottom: 20px;
  }

  &__slide-content-subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
  }
}
