// app global css in SCSS form

* {
  margin: 0;
  padding: 0;
  // box-sizing: border-box;
}

body {
  font-family: "Circe", sans-serif;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

h1 {
  font-family: "Circe Bold", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 118%;
}

h6 {
  font-family: "Circe Extra Light", sans-serif;
  font-weight: 350;
  font-size: 16px;
  line-height: 122%;
}

.container {
  // width: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px 60px;
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 124%;
    color: #333333;
    margin-bottom: 25px;
  }
}

.button {
  cursor: pointer;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cf0a2c;
  padding: 15px;
  gap: 10px;
  border: 1px solid #cf0a2c;
  transition: all 0.3s;
  // transition: color 0.3s;
}

.button:hover {
  background-color: #ffffff;
  color: #cf0a2c;
  path {
    fill: rgba(207, 10, 44, 1);
  }
}

.button:active {
  transition: all 0;
  background-color: #c60929;
  color: #ffffff;
  path {
    fill: rgb(255, 255, 255);
  }
}
