.list {
  flex-shrink: 0;
  position: relative;
  
  &__line{
    position: absolute;
    top: 45px;
    left: 8px;
    padding-top: 10px;
    height: 78%;
    border-left: #000 solid 1px;
  }

  // &__titles::after{
  //   position: absolute;
  //   top: 48px;
  //   left: 8px;
  //   content: url(../../assets/Line.svg);
  //   // border-left: solid #000 1px;
  // }

  &__title {
    margin-left: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
  }

  &__title::before {
    position: relative;
    right: 20px;
    content: url(../../assets/icons/TitleEllipse.svg);
  }

  &__subtitle {
    position: relative;
    margin: 18px 0 24px 20px;
    color: #787878;
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
  }
  &__subtitle::before {
    position: relative;
    right: 15px;
    content: url(../../assets/icons/SubtitleEllipse.svg);
  }
  &__subtitles::after {
    // position: absolute;
    // left: -15px;
    // top: -22px;
    content: url(../../assets/icons/Line.svg);
  }
  
}
