.footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: 40px 60px;
  background-color: #999999;
  color: #ffffff;

  &__politic:hover {
    cursor: pointer;
    transition: all 0.3s;
    color: rgba(155, 29, 13, 1);
  }

  &__inner {
    width: 100%;
  }

  &__info {
    font-family: "Circe";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 122%;
  }
  &__contacts {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    max-width: 250px;
    p {
      margin-bottom: 15px;
    }
  }

  h4 {
    font-family: "Ubuntu";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    border-bottom: 2px solid #cccccc;
    padding-bottom: 10px;
    width: 150px;
    margin-bottom: 30px;
  }

  &__about {
    display: flex;
    margin-bottom: 20px;
    transition: all 0.3s;

    svg {
      margin-right: 15px;
    }
    p {
      font-family: "Circe";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 122%;
    }
  }

  &__about:hover {
    cursor: pointer;
    color: rgba(155, 29, 13, 1);
    path {
      fill: rgba(155, 29, 13, 1);
    }
  }
}
