.services {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 65px;

  &__button {
    position: absolute;
    bottom: -65px;
  }

  &__img {
    background-image: url("../../assets/IconAvanta.png");
    // max-width: 90%;
    width: 420px; // assume this is the default size
    height: auto;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.partners {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
